@import url(https://fonts.googleapis.com/css2family=Lato&display=swap);
* {
  font-family: "Lato", sans-serif;
}

.container {
  display: flex;
  margin-top: 10px;
}

.link {
  text-decoration: none;
  color: inherit;
}

.topbar {
  width: 100%;
  height: 50px;
  background-color: white;
  position: sticky; /*for scrolling and not lose the navbar*/
  top: 0;
}

.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-weight: bold;
  font-size: 26px;
  color: rgb(54, 54, 54);
  cursor: pointer;
}

.topbarRight {
  display: flex;
  align-items: center;
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
}

.topIconBadge {
  width: 15px;
  height: 15px;
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #1e2444;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 9px;
}

.topAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  object-fit: cover;
}

.sidebar {
  flex: 1 1;
  height: calc(100vh - 50px);
  background-color: whitesmoke;
  position: sticky;
  top: 50px;
  border-radius: 10px;
}

.sidebarWrapper {
  padding: 20px;
  color: #555;
}

.sidebarMenu {
  margin-bottom: 10px;
}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 185);
}

.sidebarList {
  list-style: none;
  padding: 5px;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgb(232, 238, 247);
}

.sidebarIcon {
  margin-right: 10px;
  font-size: 22px !important;
}

.chart {
  margin: 20px;
  padding: 20px;
  box-shadow: 0px 5px 15px -10px rgba(0, 0, 0, 0.75);
}

.chartTitle {
  margin-bottom: 20px;
}

.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.featuredItem {
  flex: 1 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 5px 15px -10px rgba(0, 0, 0, 0.75);
}

.featuredTitle {
  font-size: 18px;
}

.featuredMoneyContainer {
  margin: 10px 0px;
  display: flex;
  align-items: center;
}

.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}

.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.featuredIcon {
  font-size: 14px;
  margin-left: 5px;
  color: green;
}

.featuredIcon.negative {
  color: brown;
}

.featuredSub {
  font-size: 15px;
  color: gray;
}

.home {
  flex: 4 1;
}

.homeWidgets {
  display: flex;
  margin: 20px;
}

.widgetSmall {
  flex: 1 1;
  box-shadow: 0px 5px 15px -10px rgba(0, 0, 0, 0.75);
  padding: 20px;
  margin-right: 20px;
}

.widgetSmallImageAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.widgetSmallList {
  list-style: none;
  margin: 0;
  padding: 0;
}

.widgetSmallListItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 0px;
}

.widgetSmallUser {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.widgetSmallUserName {
  font-weight: 600;
}

.widgetSmallUserTitle {
  font-weight: 300;
}

.widgetSmallButton {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 10px;
  padding: 7px 10px;
  background-color: #eeeef7;
  color: #555;
  cursor: pointer;
}

.widgetSmallButtonIcon {
  font-size: 16px !important;
  margin-right: 5px;
}

.widgetSmallTitle {
  font-size: 20px;
  font-weight: 600;
}

.widgetLarge {
  flex: 2 1;
  box-shadow: 0px 5px 15px -10px rgba(0, 0, 0, 0.75);
  padding: 20px;
  margin-right: 20px;
}

.widgetLargeTitle {
  font-size: 20px;
  font-weight: 600;
}

.widgetLargeImageAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.widgetLargeButton {
  padding: 5px 7px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.widgetLargeButton.Approved {
  background-color: #e5faf2;
}

.widgetLargeButton.Declined {
  background-color: #fff0f1;
}

.widgetLargeButton.Pending {
  background-color: #ebf1fe;
}

.widgetLargeTable {
  width: 100%;
  border-spacing: 20px;
}

.widgetLargeTableTh {
  text-align: left;
}

.widgetLargeTableUser {
  display: flex;
  align-items: center;
}

.widgetLargeTableUser {
  font-weight: 600;
}

.widgetLargeDate,
.widgetLargeAmount {
  font-weight: 300;
}

.userList {
  flex: 4 1;
}

.userListUser {
  display: flex;
  align-items: center;
}

.userListImage {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.userListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: rgb(12, 182, 125);
  color: white;
  cursor: pointer;
  margin-right: 10px;
}

.userListDelete {
  color: rgb(197, 66, 66);
  cursor: pointer;
}

.user {
  flex: 4 1;
  padding: 20px;
}

.userTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.userAddButton {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  border-radius: 5px;
  cursor: pointer;
  color: whitesmoke;
  font-size: 14px;
}

.userContainer {
  display: flex;
  margin-top: 20px;
}

.userShow {
  flex: 1 1;
  padding: 20px;
  box-shadow: 0px 5px 15px -10px rgba(0, 0, 0, 0.75);
}

.userUpdate {
  flex: 2 1;
  padding: 10px;
  margin-left: 20px;
  box-shadow: 0px 5px 15px -10px rgba(0, 0, 0, 0.75);
}

.userShowTop {
  display: flex;
  align-items: center;
}

.userShowImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.userShowTopTitle {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.userShowUserName {
  font-weight: 600;
}

.userShowUserTitle {
  font-weight: 300;
}

.userShowTitle {
  font-size: 14px;
  font-weight: 600;
  color: rgb(165, 165, 165);
}

.userShowBottom {
  margin-top: 20px;
}

.userShowInfo {
  display: flex;
  align-items: center;
  margin: 20px 0px;
  color: #444;
}

.userShowIcon {
  font-size: 15px !important;
}

.userShowInfoTitle {
  margin-left: 10px;
}

.userUpdateImg {
  width: 260px;
  height: 260px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 20px;
}

.userUpdateForm {
  display: flex;
  justify-content: space-between;
  margin: 20px;
  margin-top: 20px;
}

.userUpdateTitle {
  font-size: 24px;
  font-weight: 600;
  padding: 20px;
}

.userUpdateItem {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
}

.userUpdateItem > label {
  margin-bottom: 10px;
  font-size: 14px;
}

.userUpdateInput {
  border: none;
  width: 250px;
  height: 40px;
  border-bottom: 1px solid gray;
}

.userUpdateUpload {
  display: flex;
  align-items: center;
}

.userUpdateRight {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.userUpdateIcon {
  cursor: pointer;
}

.userUpdateButton {
  border: none;
  border-radius: 5px;
  height: 30px;
  border: 1px;
  padding: 5px;
  cursor: pointer;
  background-color: rgb(57, 57, 155);
  color: white;
  font-weight: 600;
}

.newUserPage {
  flex: 4 1;
  margin-left: 20px;
}

.newUserForm {
  display: flex;
  flex-wrap: wrap;
}

.newUserItem {
  width: 500px;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-right: 10px;
}

.newUserItem > label {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 600;
  color: gray;
}

.newUserItem > input {
  height: 20px;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 5px;
}

.newUserGender > input {
  margin-top: 15px;
}

.newUserGender > label {
  margin: 10px;
  font-size: 18px;
  color: gray;
}

.newUserSelect {
  height: 40px;
  border-radius: 5px;
}

.newUserButton {
  width: 200px;
  height: 40px;
  border: none;
  background-color: rgb(63, 63, 150);
  color: white;
  padding: 7px 10px;
  font-weight: 600;
  border-radius: 10px;
  margin-top: 30px;
  cursor: pointer;
  font-size: 14px;
}

.analytics {
  flex: 4 1;
  margin-left: 20px;
}

.analyticsTitle {
  font-weight: 600;
}

